/* eslint-disable @typescript-eslint/no-explicit-any */
<template>
  <div class="tag-management-container">
    <!--  -->
    <div class="tag-sidebar">
      <div class="sidebar-header">
        <h3></h3>
        <el-button type="primary" link @click="handleAddCategory">
          <el-icon><Plus /></el-icon>
        </el-button>
      </div>
      <el-tree
        :data="categoryTree"
        :props="defaultProps"
        @node-click="handleNodeClick"
        default-expand-all
      />
    </div>

    <!--  -->
    <div class="tag-content">
      <!--  -->
      <div class="dashboard-section">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card shadow="hover">
              <template #header>
                <div class="card-header">
                  <span></span>
                  <el-tag type="success"></el-tag>
                </div>
              </template>
              <div class="card-content">
                <div class="percentage">85.6%</div>
                <div class="chart-container">
                  <div ref="coverageChart" style="height: 50px;"></div>
                </div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <template #header>
                <div class="card-header">
                  <span></span>
                  <el-tag type="warning"></el-tag>
                </div>
              </template>
              <div class="card-content">
                <div ref="heatMapChart" style="height: 80px;"></div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="hover">
              <template #header>
                <div class="card-header">
                  <span>TOP5</span>
                  <el-tag type="danger"></el-tag>
                </div>
              </template>
              <div class="card-content">
                <div class="top-tags">
                  <div v-for="(tag, index) in topTags" :key="index" class="top-tag-item">
                    <span class="rank">{{ index + 1 }}</span>
                    <span class="name">{{ tag.name }}</span>
                    <span class="value">{{ tag.value }}</span>
                  </div>
                </div>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>

      <!--  -->
      <div class="operation-section">
        <el-form :inline="true" :model="searchForm" class="search-form">
          <el-form-item label="">
            <el-input v-model="searchForm.keyword" placeholder="/" clearable />
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="searchForm.permissionLevel" placeholder="" clearable>
              <el-option label="" value="1" />
              <el-option label="" value="2" />
              <el-option label="" value="3" />
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch"></el-button>
            <el-button @click="resetSearchForm"></el-button>
          </el-form-item>
        </el-form>

        <div class="operation-bar">
          <el-button type="primary" @click="handleAddTag"></el-button>
          <el-button type="success" @click="handleConfigRules"></el-button>
          <el-button type="warning" @click="handleImport"></el-button>
          <el-button type="info" @click="handleExport"></el-button>
        </div>
      </div>

      <!--  -->
      <el-table :data="tagTableData" v-loading="loading" border style="width: 100%">
        <el-table-column type="selection" width="55" />
        <el-table-column prop="tagName" label="" min-width="120" />
        <el-table-column prop="tagCode" label="" min-width="120" />
        <el-table-column prop="type" label="" width="100">
          <template #default="{ row }">
            <el-tag :type="getTypeTagType(row.type)">{{ row.type }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="valueRange" label="" min-width="150" show-overflow-tooltip />
        <el-table-column prop="permissionLevel" label="" width="100">
          <template #default="{ row }">
            <el-tag :type="getPermissionTagType(row.permissionLevel)">
              {{ `${row.permissionLevel}` }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="" width="100">
          <template #default="{ row }">
            <el-tag :type="row.status === '' ? 'success' : 'info'">{{ row.status }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="updatedAt" label="" width="160" />
        <el-table-column label="" width="250" fixed="right">
          <template #default="{ row }">
            <el-button link type="primary" @click="handleEditTag(row)"></el-button>
            <el-button link type="success" @click="handleConfigRules(row)"></el-button>
            <el-button link type="warning" @click="handleToggleStatus(row)">
              {{ row.status === '' ? '' : '' }}
            </el-button>
            <el-button link type="danger" @click="handleDeleteTag(row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="pagination-container">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[10, 20, 50, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>

    <!--  -->
    <el-dialog
      v-model="tagFormDialogVisible"
      :title="dialogTitle"
      width="650px"
      :close-on-click-modal="false"
      @close="handleCloseDialog"
    >
      <el-form
        ref="tagFormRef"
        :model="tagForm"
        :rules="tagFormRules"
        label-width="100px"
      >
        <el-form-item label="" prop="tagName">
          <el-input v-model="tagForm.tagName" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="tagCode">
          <el-input v-model="tagForm.tagCode" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="tagForm.type" placeholder="">
            <el-option label="" value="STRING" />
            <el-option label="" value="NUMBER" />
            <el-option label="" value="DATE" />
            <el-option label="" value="BOOLEAN" />
            <el-option label="" value="ENUM" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="valueRange">
          <el-input
            v-model="tagForm.valueRange"
            placeholder="0-100"
            v-if="tagForm.type === 'NUMBER'"
          />
          <el-select
            v-model="tagForm.valueRange"
            multiple
            placeholder=""
            v-else-if="tagForm.type === 'ENUM'"
            allow-create
            filterable
          />
          <el-input v-else disabled placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="permissionLevel">
          <el-select v-model="tagForm.permissionLevel" placeholder="">
            <el-option label="" value="1" />
            <el-option label="" value="2" />
            <el-option label="" value="3" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            v-model="tagForm.description"
            type="textarea"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleCloseDialog"></el-button>
          <el-button type="primary" @click="handleSubmitForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      v-model="ruleDialogVisible"
      title=""
      width="800px"
      :close-on-click-modal="false"
    >
      <div class="rule-config">
        <div class="condition-group">
          <div class="group-header">
            <span></span>
            <el-radio-group v-model="ruleForm.logic" size="small">
              <el-radio-button label="AND"></el-radio-button>
              <el-radio-button label="OR"></el-radio-button>
            </el-radio-group>
          </div>
          <div class="conditions">
            <div v-for="(condition, index) in ruleForm.conditions" :key="index" class="condition-item">
              <el-select v-model="condition.field" placeholder="">
                <el-option label="" value="age" />
                <el-option label="" value="amount" />
                <el-option label="" value="visits" />
              </el-select>
              <el-select v-model="condition.operator" placeholder="">
                <el-option label="" value=">" />
                <el-option label="" value="<" />
                <el-option label="" value="=" />
              </el-select>
              <el-input v-model="condition.value" placeholder="" />
              <el-button type="danger" link @click="removeCondition(index)">
                <el-icon><Delete /></el-icon>
              </el-button>
            </div>
          </div>
          <el-button type="primary" link @click="addCondition">
            <el-icon><Plus /></el-icon> 
          </el-button>
        </div>
        
        <div class="time-window">
          <div class="window-header"></div>
          <el-form :model="ruleForm.timeWindow" label-width="100px">
            <el-form-item label="">
              <el-select v-model="ruleForm.timeWindow.period">
                <el-option label="1" value="1d" />
                <el-option label="7" value="7d" />
                <el-option label="30" value="30d" />
                <el-option label="" value="custom" />
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="ruleForm.timeWindow.aggregation">
                <el-option label="" value="sum" />
                <el-option label="" value="avg" />
                <el-option label="" value="max" />
                <el-option label="" value="min" />
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="ruleDialogVisible = false"></el-button>
          <el-button type="primary" @click="saveRuleConfig"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, onBeforeUnmount } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import * as echarts from 'echarts/core'
import { LineChart, HeatmapChart } from 'echarts/charts'
import {
  GridComponent,
  TooltipComponent,
  VisualMapComponent
} from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'

// 
echarts.use([
  LineChart,
  HeatmapChart,
  GridComponent,
  TooltipComponent,
  VisualMapComponent,
  CanvasRenderer
])

// 
const loading = ref(false)
const currentPage = ref(1)
const pageSize = ref(10)
const total = ref(0)
const tagTableData = ref([])
const searchForm = reactive({
  keyword: '',
  permissionLevel: ''
})

// 
const coverageChart = ref(null)
const heatMapChart = ref(null)

// 
let coverageChartInstance = null
let heatMapChartInstance = null

// 
const categoryTree = ref([
  {
    id: 1,
    label: '',
    children: [
      { id: 11, label: '' },
      { id: 12, label: '' }
    ]
  },
  {
    id: 2,
    label: '',
    children: [
      { id: 21, label: '' },
      { id: 22, label: '' }
    ]
  }
])

const defaultProps = {
  children: 'children',
  label: 'label'
}

// TOP
const topTags = ref([
  { name: 'VIP', value: '98%' },
  { name: '', value: '85%' },
  { name: '', value: '82%' },
  { name: '', value: '78%' },
  { name: '', value: '75%' }
])

// 
const tagFormDialogVisible = ref(false)
const ruleDialogVisible = ref(false)
const dialogMode = ref('add')
const tagFormRef = ref(null)
const tagForm = reactive({
  tagName: '',
  tagCode: '',
  type: '',
  valueRange: '',
  permissionLevel: '',
  description: ''
})

const ruleForm = reactive({
  logic: 'AND',
  conditions: [
    { field: '', operator: '', value: '' }
  ],
  timeWindow: {
    period: '7d',
    aggregation: 'sum'
  }
})

const tagFormRules = {
  tagName: [{ required: true, message: '', trigger: 'blur' }],
  tagCode: [{ required: true, message: '', trigger: 'blur' }],
  type: [{ required: true, message: '', trigger: 'change' }],
  valueRange: [{ required: true, message: '', trigger: 'blur' }],
  permissionLevel: [{ required: true, message: '', trigger: 'change' }]
}

// 
const dialogTitle = computed(() => {
  return dialogMode.value === 'add' ? '' : ''
})

// 
const getTypeTagType = (type) => {
  const typeMap = {
    'STRING': '',
    'NUMBER': 'success',
    'DATE': 'warning',
    'BOOLEAN': 'info',
    'ENUM': 'danger'
  }
  return typeMap[type] || ''
}

const getPermissionTagType = (level) => {
  const levelMap = {
    '1': 'danger',
    '2': 'warning',
    '3': 'info'
  }
  return levelMap[level] || ''
}

// 
const initCharts = () => {
  // 
  coverageChartInstance = echarts.init(coverageChart.value)
  coverageChartInstance.setOption({
    grid: { top: 0, right: 0, bottom: 0, left: 0 },
    xAxis: {
      type: 'category',
      show: false,
      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
    },
    yAxis: { type: 'value', show: false },
    series: [{
      data: [82, 83, 84, 84, 85, 85, 85.6],
      type: 'line',
      smooth: true,
      symbol: 'none',
      areaStyle: {
        color: {
          type: 'linear',
          x: 0, y: 0, x2: 0, y2: 1,
          colorStops: [
            { offset: 0, color: 'rgba(64,158,255,0.3)' },
            { offset: 1, color: 'rgba(64,158,255,0.1)' }
          ]
        }
      },
      lineStyle: { color: '#409EFF' }
    }]
  })

  // 
  heatMapChartInstance = echarts.init(heatMapChart.value)
  heatMapChartInstance.setOption({
    grid: { top: 0, right: 0, bottom: 0, left: 0 },
    xAxis: {
      type: 'category',
      show: false,
      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
    },
    yAxis: {
      type: 'category',
      show: false,
      data: ['']
    },
    visualMap: {
      show: false,
      min: 0,
      max: 10,
      calculable: true,
      orient: 'horizontal',
      inRange: {
        color: ['#e6f3ff', '#409EFF']
      }
    },
    series: [{
      type: 'heatmap',
      data: [[0, 0, 5], [1, 0, 7], [2, 0, 3], [3, 0, 8], [4, 0, 6], [5, 0, 4], [6, 0, 9]],
      label: { show: false }
    }]
  })
}

// 
const handleResize = () => {
  coverageChartInstance?.resize()
  heatMapChartInstance?.resize()
}

// 
const fetchTags = async () => {
  loading.value = true
  try {
    // API
    await new Promise(resolve => setTimeout(resolve, 500))
    tagTableData.value = [
      {
        id: 1,
        tagName: 'VIP',
        tagCode: 'VIP_USER',
        type: 'BOOLEAN',
        valueRange: 'true/false',
        permissionLevel: '1',
        status: '',
        updatedAt: '2024-03-20 10:00:00'
      },
      {
        id: 2,
        tagName: '',
        tagCode: 'CONSUMPTION_AMOUNT',
        type: 'NUMBER',
        valueRange: '0-999999',
        permissionLevel: '2',
        status: '',
        updatedAt: '2024-03-20 11:00:00'
      }
    ]
    total.value = tagTableData.value.length
  } catch (error) {
    ElMessage.error('')
    console.error('Failed to fetch tags:', error)
  } finally {
    loading.value = false
  }
}

const handleSearch = () => {
  currentPage.value = 1
  fetchTags()
}

const resetSearchForm = () => {
  searchForm.keyword = ''
  searchForm.permissionLevel = ''
  currentPage.value = 1
  fetchTags()
}

const handleNodeClick = (data) => {
  console.log(data)
  // 
}

const handleAddCategory = () => {
  // 
  ElMessage.info('')
}

const handleAddTag = () => {
  dialogMode.value = 'add'
  Object.keys(tagForm).forEach(key => {
    tagForm[key] = ''
  })
  tagFormDialogVisible.value = true
}

const handleEditTag = (row) => {
  dialogMode.value = 'edit'
  Object.assign(tagForm, row)
  tagFormDialogVisible.value = true
}

const handleConfigRules = (row) => {
  ruleDialogVisible.value = true
  // row
  if (row) {
    console.log('Loading rules for tag:', row)
  }
}

const handleDeleteTag = (row) => {
  ElMessageBox.confirm(
    `"${row.tagName}"`,
    '',
    {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }
  ).then(async () => {
    try {
      await new Promise(resolve => setTimeout(resolve, 500))
      tagTableData.value = tagTableData.value.filter(item => item.id !== row.id)
      total.value = tagTableData.value.length
      ElMessage.success('')
    } catch (error) {
      ElMessage.error('')
      console.error('Failed to delete tag:', error)
    }
  })
}

const handleToggleStatus = async (row) => {
  try {
    await new Promise(resolve => setTimeout(resolve, 500))
    row.status = row.status === '' ? '' : ''
    ElMessage.success(`${row.status === '' ? '' : ''}`)
  } catch (error) {
    ElMessage.error(`${row.status === '' ? '' : ''}`)
    console.error('Failed to toggle status:', error)
  }
}

const handleCloseDialog = () => {
  tagFormDialogVisible.value = false
  tagFormRef.value?.resetFields()
}

const handleSubmitForm = () => {
  tagFormRef.value?.validate(async (valid) => {
    if (valid) {
      try {
        await new Promise(resolve => setTimeout(resolve, 500))
        if (dialogMode.value === 'add') {
          const newTag = {
            id: Date.now(),
            ...tagForm,
            status: '',
            updatedAt: new Date().toLocaleString()
          }
          tagTableData.value.unshift(newTag)
        } else {
          const index = tagTableData.value.findIndex(item => item.id === tagForm.id)
          if (index !== -1) {
            tagTableData.value[index] = {
              ...tagTableData.value[index],
              ...tagForm,
              updatedAt: new Date().toLocaleString()
            }
          }
        }
        total.value = tagTableData.value.length
        ElMessage.success(`${dialogMode.value === 'add' ? '' : ''}`)
        handleCloseDialog()
      } catch (error) {
        ElMessage.error(`${dialogMode.value === 'add' ? '' : ''}`)
        console.error('Failed to save tag:', error)
      }
    }
  })
}

const addCondition = () => {
  ruleForm.conditions.push({ field: '', operator: '', value: '' })
}

const removeCondition = (index) => {
  ruleForm.conditions.splice(index, 1)
}

const saveRuleConfig = async () => {
  try {
    await new Promise(resolve => setTimeout(resolve, 500))
    ElMessage.success('')
    ruleDialogVisible.value = false
  } catch (error) {
    ElMessage.error('')
    console.error('Failed to save rule config:', error)
  }
}

const handleImport = () => {
  ElMessage.info('')
}

const handleExport = () => {
  ElMessage.info('')
}

const handleSizeChange = (val) => {
  pageSize.value = val
  currentPage.value = 1
  fetchTags()
}

const handleCurrentChange = (val) => {
  currentPage.value = val
  fetchTags()
}

// 
onMounted(() => {
  fetchTags()
  initCharts()
  window.addEventListener('resize', handleResize)
})

onBeforeUnmount(() => {
  window.removeEventListener('resize', handleResize)
  coverageChartInstance?.dispose()
  heatMapChartInstance?.dispose()
})
</script>

<style scoped>
.tag-management-container {
  padding: 20px;
  background-color: #fff;
  border-radius: 4px;
  min-height: calc(100vh - 120px);
  display: flex;
}

.tag-sidebar {
  width: 250px;
  margin-right: 20px;
  border-right: 1px solid #e4e7ed;
  padding-right: 20px;
}

.sidebar-header {
  padding: 10px;
  background-color: #f5f7fa;
  border-bottom: 1px solid #e4e7ed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.tag-content {
  flex: 1;
}

.dashboard-section {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-content {
  position: relative;
}

.percentage {
  font-size: 24px;
  font-weight: bold;
  color: #409EFF;
  margin-bottom: 10px;
}

.top-tags .top-tag-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.top-tags .top-tag-item .rank {
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  background-color: #f5f7fa;
  border-radius: 50%;
  margin-right: 8px;
}

.top-tags .top-tag-item .name {
  flex: 1;
  margin-right: 8px;
}

.top-tags .top-tag-item .value {
  color: #909399;
}

.operation-section {
  margin-bottom: 20px;
}

.search-form {
  margin-bottom: 20px;
  padding: 20px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.operation-bar {
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}

.rule-config {
  margin-bottom: 20px;
}

.rule-config .condition-group {
  margin-bottom: 20px;
}

.rule-config .condition-group .group-header {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 15px;
}

.rule-config .condition-group .conditions {
  margin-bottom: 10px;
}

.rule-config .condition-group .conditions .condition-item {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  align-items: center;
}

.rule-config .condition-group .conditions .condition-item .el-select {
  width: 150px;
}

.rule-config .condition-group .conditions .condition-item .el-input {
  width: 150px;
}

.rule-config .time-window {
  border-top: 1px solid #e4e7ed;
  padding-top: 20px;
}

.rule-config .time-window .window-header {
  margin-bottom: 15px;
  font-weight: bold;
}

:deep(.el-table) {
  margin-top: 10px;
}

:deep(.el-tag) {
  margin-right: 8px;
}
</style> 